:root {
    --app-background: #fcfcfc;
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

.flex {
    display: flex;
}

.justify-between {
    justify-content: space-between;
}

section {
    margin-top: 60px;
}

h1, li, a, button, nav{
    color: #fff;
}

body {
    max-height: 100svh;
}

main {
    background-color: #fcfcfc;
}

header {
    height: 60px;
    background-color: #e1401c;

    h1 {
        font-size: 2.4rem;
        font-weight: 400;
        line-height: 60px;
        width: 30%;
        margin: 0;
    }

    nav {
        line-height: 60px;
        width: 40%;
        font-size: 2.2rem;
    }
}

.navigation {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
}

.navigation a {
    text-decoration: none;
}

.banner-button {
    width: 100%;
    height: 28rem;
    display: flex;
    flex-direction: column;

    .banner {
        text-align: center;
        margin-top: 40px;
    }

    .banner h2, p {
        color: #000;
    }
}

.banner h2 {
    font-size: 3rem;
}

.banner p {
    font-size: 2rem;
    margin-top: 15px;
    letter-spacing: 2px;
}

.prime-second {
    height: 10rem;
    display: flex;
    justify-content: center;
    margin-top: 40px;
}

.primeBtn {
    width: 12rem;
    height: 3.5rem;
    font-size: 1.6rem;
    border-radius: 50px;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #e54e2c;
    color: #fcfcfc;
}

.secondBtn {
    width: 12rem;
    height: 3.5rem;
    margin-left: 15px;
    font-size: 1.6rem;
    border-radius: 50px;
    text-transform: uppercase;
    cursor: pointer;
    border: 1px solid #e54e2c;
    color: #e54e2c;
    background-color: var(--app-background);
}

.gap-15 {
    gap: 20px;
}

.flex { 
    display: flex;
}

.imageGallery {
    width: 100%;
    height: 35rem;
    background-color: #f7f7f7;
}

.list {
    margin-top: 30px;
}

.imageGallery .images img {
    display: flex;
    justify-content: space-evenly;
    width: 250px;  
    height: 30rem; 
    margin-top: 20px;
    border-radius: 5px;
}

footer {
    width: 100%;
    height: 85px;
    background-color: #f2f2f2;
}

.footerContent {
    width: 100%;
    text-align: center;
    line-height: 85px;
    font-size: 18px;
}

.fa-copyright {
    font-size: 10px;
    margin-right: 5px;
}
